.contenido{
    background-color: #222227;
    height: 100vh;
    color: #FFFFFF;
    border-radius: 50px;
    text-align: center;
    position: relative;
    overflow: auto;
}
.flotante-naranja{
    width: 100px;
    height: 20px;
    position: absolute;
    background-color: #EFB322;
    filter: blur(50px);
    top: 0;
    left: 0;
    border-top-left-radius: 50px;
}

.mainContenido{
    position: relative;
    height: 53vh;
    overflow: auto;
    margin-top: 6%;
    padding-top: 20px;
    padding-bottom: 20px;
}

h4{
    font-family: 'Courier New', Courier, monospace;
    letter-spacing: -2px;
}
.pTiny{
    font-size: 14px;
}



.mainContenido::-webkit-scrollbar {
    -webkit-appearance: none;
}

.mainContenido::-webkit-scrollbar:vertical {
    width:5px;
}

.mainContenido::-webkit-scrollbar-button:increment,.contenedor::-webkit-scrollbar-button {
    display: none;
} 

.mainContenido::-webkit-scrollbar:horizontal {
    height: 10px;
}

.mainContenido::-webkit-scrollbar-thumb {
    background-color: #EFB322;
    border-radius: 20px;
}

.mainContenido::-webkit-scrollbar-track {
    border-radius: 10px;  
}










footer{
    height: 80px;
    position: fixed;
    bottom: 0;
    background-color: #222227;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;
}
.imgFil{
    filter: invert(0.4) sepia(1) hue-rotate(20deg) saturate(0%);  
    cursor: pointer;
}
.btn-back{
    z-index: 2;
}
.circle-warning{
    position: absolute;
    width: 40px;
    height: 40px;
    background-color: #EFB322;
    margin: auto;
    bottom: 0;
    top: 0;
    border-radius: 50%;
    z-index: 1;
    box-shadow: 1px 2px 3px 0px #EFB322;
}

@media screen and (max-width: 510px) {
    .contenido{
        border-radius: 0px;
    }
}